import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { Router } from '@angular/router';
import { ModalController } from '@ionic/angular';

import { AppLogService } from '../../include/app-log/app-log.service';
import { HostDomain } from '../../include/app-settings/app-settings.module';
import { Translations } from '../../include/i18n/i18n.module';

@Component({
    selector: 'menu-modal',
    templateUrl: './menu-modal.page.html',
    styleUrls: ['./menu-modal.page.scss'],
})
export class MenuModalPage implements OnInit {

	@Output() navigating = new EventEmitter<void>();

	hostDomain = HostDomain;

    constructor(
    	private log: AppLogService,
        private modal: ModalController,
        private nav: Router,
        translate: Translations,
    ) {
		translate.ready([
			Translation.Home,
			Translation.About,
			Translation.Help,
			Translation.Terms,
			Translation.Shop,
		]).then( () => {
			let route = this.nav.url;

			this.items.push({ title: translate.get(Translation.Home), route: "/home", active: false });
			this.items.push({ title: translate.get(Translation.Shop), route: "/shop", active: false });
			this.items.push({ title: translate.get(Translation.About), route: "/about", active: false });
			this.items.push({ title: translate.get(Translation.Terms), route: "/terms", active: false });
			this.items.push({ title: translate.get(Translation.Help), route: "/help", active: false });

			for (let i = 0; i < this.items.length; i++) {
				let item = this.items[i];

				item.active = item.route === route;
			}
		});
    }

    ngOnInit() {
    }


	items: MenuItem[] = [];

	navigate(item: MenuItem) {
		if (item.route == "/terms") {
			window.open(`/eula`, "_blank");
		} else if (item.route === '/help') {
			this.log.trace("navigating from", this.nav.url);
	        this.nav.navigateByUrl(item.route, {state: {referer: this.nav.url}});
			this.navigating.emit();
		} else {
			this.nav.navigate([item.route]);
			this.navigating.emit();
		}

		setTimeout( () => this.modal.dismiss(), 500);
	}

    close() {
        this.modal.dismiss();
    }
}

interface MenuItem {
	title: string,
	route: string,
	active: boolean,
}

const Translation = {
	Home: "App_Menu.Home",
	About: "App_Menu.About",
	Help: "App_Menu.Help",
	Terms: "App_Menu.Terms",
	Shop: "App_Menu.Shop",
}